import { useFormik } from 'formik'
import React, { useState } from 'react'
import './Register.css'
import * as Yup from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
export default function Register() {
  let [errMessage, seterrMessage] = useState("")
  let [Loading, setLoading] = useState(false)
  let navigate = useNavigate()
  let baseURL = "https://route-ecommerce.onrender.com"
  let validationSchema = Yup.object({
    name: Yup.string().required('name is required').min(2, "min char 2").max(10, "max char 10"),
    email: Yup.string().email("enter Valid Email").required('email is required'),
    phone: Yup.string().required('phone is required').matches(/^(010|011|012|015)[0-9]{8}$/, "enter Valid Phone"),
    password: Yup.string().min(8, 'Password must be at least 8 characters').matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/, 'Password must contain at least one uppercase letter, one lowercase letter, and one number').required('Password is required'),
    rePassword: Yup.string().required('rePassword is required').oneOf([Yup.ref('password')], "Re password not match")
  })
  let formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      password: '',
      rePassword: '',
      phone: '',
    },
    onSubmit: (values) => {
      sendDataRegister(values)
    },
    validationSchema,
  })

  async function sendDataRegister(userData) {
    setLoading(true)
    let { data } = await axios.post(`${baseURL}/api/v1/auth/signup`, userData).catch((error) => {
      seterrMessage(error.response.data.errors.msg)
      setLoading(false)
    })
    setLoading(false)
    if (data.message == "success") {
      navigate("/login")
    }
  }



  return <>
    <div className="container py-5">
      <form onSubmit={formik.handleSubmit} className="w-75 m-auto">
        <h2>Register Now :</h2>
        <div className="my-3">
          <label htmlFor="name">name :</label>
          <input onChange={formik.handleChange} type="text" id='name' className="form-control" />
          <p className="text-danger">{formik.errors.name}</p>
        </div>
        <div className="my-3">
          <label htmlFor="email">email :</label>
          <input onChange={formik.handleChange} type="email" id='email' className="form-control" />
          <p className="text-danger">{formik.errors.email}</p>
        </div>
        <div className="my-3">
          <label htmlFor="password">password :</label>
          <input onChange={formik.handleChange} type="password" id='password' className="form-control" />
          <p className="text-danger">{formik.errors.password}</p>
        </div>
        <div className="my-3">
          <label htmlFor="rePassword">rePassword :</label>
          <input onChange={formik.handleChange} type="password" id='rePassword' className="form-control" />
          <p className="text-danger">{formik.errors.rePassword}</p>
        </div>
        <div className="my-3">
          <label htmlFor="phone">phone :</label>
          <input onChange={formik.handleChange} type="text" id='phone' className="form-control" />
          <p className="text-danger">{formik.errors.phone}</p>
        </div>
        {errMessage != "" ? <div className='alert alert-danger'>{errMessage}</div> : ""}
        {Loading ? <button type="button" className="btn float-end bg-btn text-white"><i className='fa-solid fa-spinner fa-spin text-white'></i></button> : <button type="submit" disabled={!formik.isValid} className="btn float-end bg-btn text-white">Register</button>}
      </form>
    </div>
  </>
}
