import { useFormik } from 'formik'
import React, { useState } from 'react'
import * as Yup from 'yup'
import axios from 'axios'

import { Link, useNavigate } from 'react-router-dom'
export default function Login({saveUserData}) {
  let [errMessage, seterrMessage] = useState("")
  let [Loading, setLoading] = useState(false)
  let navigate = useNavigate()
  let baseURL = "https://route-ecommerce.onrender.com"
  let validationSchema = Yup.object({
    email: Yup.string().email("enter Valid Email").required('email is required'),
    password: Yup.string().required('Password is required'),
  })
  let formik = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    onSubmit: (values) => {
      sendDataLogin(values)
    },
    validationSchema,
  })

  async function sendDataLogin(userData) {
    setLoading(true)
    let { data } = await axios.post(`${baseURL}/api/v1/auth/signin`, userData).catch((error) => {
      seterrMessage(error.response.data.message)
      setLoading(false)
    })
    setLoading(false)
    if (data.message == "success") {
      localStorage.setItem("token", data.token)
      saveUserData(data.user)
      navigate("/home")
    }
  }



  return <>
    <div className="container py-5">
      <form onSubmit={formik.handleSubmit} className="w-75 m-auto">
        <h2>Login :</h2>
        <div className="my-3">
          <label htmlFor="email">email :</label>
          <input onChange={formik.handleChange} type="email" id='email' className="form-control" />
          <p className="text-danger">{formik.errors.email}</p>
        </div>
        <div className="my-3">
          <label htmlFor="password">password :</label>
          <input onChange={formik.handleChange} type="password" id='password' className="form-control" />
          <p className="text-danger">{formik.errors.password}</p>
        </div>
        {errMessage != "" ? <div className='alert alert-danger'>{errMessage}</div> : ""}
        <Link className="text-decoration-none main-color" to={"/forgetPassword"}>Forget Password ?</Link>
        <br/>
        {Loading ? <button type="button" className="btn float-end bg-btn text-white"><i className='fa-solid fa-spinner fa-spin text-white'></i></button> : <button type="submit" disabled={!formik.isValid} className="btn float-end bg-btn text-white">Login</button>}
      </form>
    </div>
  </>
}
