import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Category from '../Category/Category'
import MainSlider from '../MainSlider/MainSlider'
import $ from "jquery"
export default function Home() {
  let baseURL = "https://route-ecommerce.onrender.com"
  let [productsList,setproductsList] = useState([])
  useEffect(()=> {
    getAllproducts()
  },[])
 async function getAllproducts() {
      let { data } = await axios.get(`${baseURL}/api/v1/products`)
      setproductsList(data.data)
      $(".Loading").fadeOut(2000)
      }

  return <>
  <MainSlider/>
  <Category/>
  <div className='position-fixed top-0 end-0 start-0 bottom-0 bg-white Loading justify-content-center align-items-center'>
  <span class="loader"></span>
  </div>
  <div className="row g-4 my-5">
    {productsList.map((product) => {
   return <div key={product._id} className="col-md-2">
    <Link to={"/productDetails/"+product._id}>
      <div className="product">
        <img src={product.imageCover} className='w-100' alt="" />
        <span className='main-color'>{product.category.name}</span>
        <h2 className='h6'>{product.title.split(" ").slice(0,2).join(" ")}</h2>
      <div className='d-flex justify-content-between'>
      <p>{product.price}EGP</p>
        <div>
          <i className='fa-solid fa-star text-warning'></i>{product.ratingsAverage}
        </div>
      </div>
      </div>
      </Link>
    </div>
    })}
  </div>
  </>
}
