import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import slider_img1 from '../../assetes/images/slider-image-1.jpeg'
import slider_img2 from '../../assetes/images/slider-image-2.jpeg'
import slider_img3 from '../../assetes/images/slider-image-3.jpeg'
export default function MainSlider() {
  return <>
  <div className="py-3">
    <div className="row g-0">
  <div className="col-md-9">

    <OwlCarousel className='owl-theme' autoplay={true} autoplayTimeout={5000} items={1} loop>
    <div>
    <img src={slider_img3} className='img_slider w-100' alt="" />
    </div>
    <div>
    <img src={slider_img2} className='img_slider w-100' alt="" />
    </div>
    <div>
    <img src={slider_img1} className='img_slider w-100' alt="" />
    </div>
</OwlCarousel>;
  </div>
  <div className="col-md-3">
  <img src={slider_img1} className='img_slider2 w-100' alt="" />
  <img src={slider_img2} className='img_slider2 w-100' alt="" />
  </div>
  </div>
  </div>
  </>
}
