import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as Yup from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
export default function ForgetPassword() {
    let navigate = useNavigate()
    let [errMessage, seterrMessage] = useState("")
    let [errMes, seterrMes] = useState("")
    let baseURL = "https://route-ecommerce.onrender.com"
    let [Loading, setLoading] = useState(false)
    let [CodeFlag, setCodeFlag] = useState(true)
    let validationSchema = Yup.object({
        email: Yup.string().email('enter valid Email').required('Email is required'),
    })
    let Form1 = useFormik({
    initialValues:{
        email:""
    },
    onSubmit:(values)=>{
        forgetPassword(values)
    },
    validationSchema,
    })
    let Form2 = useFormik({
    initialValues:{
        resetCode:""
    },
    onSubmit:(values)=>{
        resetCode(values)
    },
    })
    async function forgetPassword(userEmail){
        setLoading(true)
    let { data } = await axios.post(`${baseURL}/api/v1/auth/forgotPasswords`, userEmail).catch((error) => {
    seterrMessage(error.response.data.message)
    setLoading(false)
    })
    setLoading(false)
    if (data.statusMsg == "success") {
        setCodeFlag(false)  
        seterrMessage("")   
    }
}
    async function resetCode(userCode){
setLoading(true)
    let { data } = await axios.post(`${baseURL}/api/v1/auth/verifyResetCode`, userCode).catch((error) => {
        seterrMes(error.response.data.message)
      setLoading(false)
    })
    setLoading(false)
    if (data.status == "Success") {
        navigate("/resetPasswoerd")
    }
}
  return <>

 <div className="container py-5">
   
{CodeFlag?  <form onSubmit={Form1.handleSubmit}>
        <div>
            <label htmlFor="email">email</label>
            <input onChange={Form1.handleChange} type="email" id='email' className="form-control" />
          <p className="text-danger">{Form1.errors.email}</p>
        </div>
        {errMessage != "" ? <div className='alert alert-danger'>{errMessage.split(" ").splice(0,8).join(" ")}</div> : ""}
        {Loading ? <button type="button" className="btn float-end bg-btn text-white"><i className='fa-solid fa-spinner fa-spin text-white'></i></button> : <button type="submit" disabled={!Form1.isValid} className="btn float-end bg-btn text-white">send Massage</button>}
    </form>: <form onSubmit={Form2.handleSubmit}>
        <div className='w-50 m-auto'>
            <label htmlFor="resetCode">resetCode</label>
            <input placeholder="######" type="text" id='resetCode' className="form-control text-center fs-2" />
        </div>
        {errMes != "" ? <div className='alert alert-danger'>{errMes.split(" ").splice(0,8).join(" ")}</div> : ""}
        {Loading ? <button type="button" className="btn float-end bg-btn text-white"><i className='fa-solid fa-spinner fa-spin text-white'></i></button> : <button type="submit" disabled={!Form2.isValid} className="btn float-end bg-btn text-white">verify Code</button>}
    </form>}
   
 </div>
  
  </>
}
