import React from 'react'
import com from "../../assetes/com.png"
import mobileApp from "../../assetes/mobileApp.PNG"
export default function Footer() {
  return (
    <div className="bg-light p-5 mt-5">
      <h3>Get the FreshCart app</h3>
      <p className="text-muted">We wilkl send you a link, open it on your phone to download the app</p>
      <div className="row ps-5">
        <div className="col-md-10">
          <input type="email" placeholder='Email' className='form-control' />
        </div>
        <div className="col-md-2">
          <button className='btn bg-btn text-white w-100'>Share App Link</button>
        </div>
      </div>
      <div className='border-bottom border-top border-1 row justify-content-between mt-5 mb-3 py-3'>
<div className="col-md-5 d-flex">
  <h6 className='me-2 mt-3'>Payment Partners</h6>
  <img src={com} className="w-50 mt-1" alt="" />
  </div>
<div className="col-md-4 d-flex">
<h6 className='me-2 mt-3'>Get deliveries with FreshCart</h6>
<img src={mobileApp} className="w-50" alt="" />
</div>
      </div>
    </div>
  )
}
