import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import $ from "jquery"
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
export default function ProductDetails() {
 let {id} = useParams()
 let baseURL = "https://route-ecommerce.onrender.com"
 let [productDetail,setproductDetail] = useState()


 useEffect(()=> {
  getAllproductDetail()
 },[])
async function getAllproductDetail() {
     let { data } = await axios.get(`${baseURL}/api/v1/products/${id}`)
     setproductDetail(data.data)
     $(".Loading").fadeOut(1000)
     }
  return <>
  <div className='position-fixed top-0 end-0 start-0 bottom-0 bg-white Loading justify-content-center align-items-center'>
  <span class="loader"></span>
  </div>
  {productDetail ? <div className="row align-items-center">
    <div className="col-md-4">
    <OwlCarousel className='owl-theme'  loop items={1}>
                        {productDetail.images.map((el) => {
                            return <div >
                                <img  src={el} />
                            </div>
                        })}
                    </OwlCarousel>
    </div>
    <div className="col-md-8">
      <h2>{productDetail.title}</h2>
      <p className="text-muted">{productDetail.description}</p>
      <span className='main-color'>{productDetail.category.name}</span>
      <div className='d-flex justify-content-between'>
      <p>{productDetail.price}EGP</p>
        <div>
          <i className='fa-solid fa-star text-warning'></i>{productDetail.ratingsAverage}
        </div>
      </div>
      <button className="btn bg-btn text-white w-100">+ Add Cart</button>
    </div>
  </div>:""}
  </>
}
