import React, { useEffect, useState } from 'react'
import { createBrowserRouter, Navigate, RouterProvider } from 'react-router-dom'
import Layout from "./Component/Layout/Layout"
import Home from "./Component/Home/Home"
import Login from './Component/Login/Login'
import Notfound from './Component/Notfound/Notfound'
import Register from './Component/Register/Register'
import Profile from './Component/Profile/Profile'
import jwtDecode from 'jwt-decode'
import ForgetPassword from './Component/ForgetPassword/ForgetPassword'
import ResetPassword from './Component/ForgetPassword/ResetPassword'
import ProductDetails from './Component/ProductDetails/ProductDetails'
export default function App() {

  let [userdata,setuserdata]= useState(null)

useEffect(()=>{
  if(localStorage.getItem("token")){
    let token = localStorage.getItem("token")
    let data = jwtDecode(token)
    setuserdata(data)
  }
},[])

function ProtecteRouting(props){
  if (localStorage.getItem("token")){
    return props.children
  }
  else{
    return <Navigate to="/login"/>
}}
function ProtecteRouting1(props){
  if (localStorage.getItem("token")){
    return <Navigate to="/home"/>  
  }
  else{
    return props.children
}}

function Logout(){
  setuserdata(null)
  localStorage.removeItem("token")      
  return <Navigate to="/login"/>
}

  function saveUserData (data){
    setuserdata(data)
  }
  let routes = createBrowserRouter([
    { path: "", element:<Layout Logout={Logout} userdata={userdata}/>,children:[
      {path: "home", element:<ProtecteRouting><Home/></ProtecteRouting> },
      {path: "profile", element:<ProtecteRouting><Profile userdata={userdata}/></ProtecteRouting> },
      {path: "productDetails/:id", element:<ProtecteRouting><ProductDetails/></ProtecteRouting> },
      {path: "login", element:<Login saveUserData={saveUserData}/>},
      {index: true, element:<ProtecteRouting1><Register/></ProtecteRouting1>},
      {path: "forgetPassword", element:<ForgetPassword/>},
      {path: "resetPasswoerd", element:<ResetPassword/>},
      {path: "*", element:<Notfound/>},
    ]},
  ])
  return (
   <RouterProvider router={routes}/>
  )
}
