import { useFormik } from 'formik'
import React, { useState } from 'react'
import * as Yup from 'yup'
import axios from 'axios'
import { Link, useNavigate } from 'react-router-dom'
export default function ResetPassword() {
  let [errMessage, seterrMessage] = useState("")
  let [Loading, setLoading] = useState(false)
  let navigate = useNavigate()
  let baseURL = "https://route-ecommerce.onrender.com"
  let validationSchema = Yup.object({
    email: Yup.string().email("enter Valid Email").required('email is required'),
    newPassword: Yup.string().required('Password is required'),
  })
  let formik = useFormik({
    initialValues: {
      email: '',
      newPassword: '',
    },
    onSubmit: (values) => {
      resetPassword(values)
    },
    validationSchema,
  })

  async function resetPassword(userData) {
    setLoading(true)
    let { data } = await axios.put(`${baseURL}/api/v1/auth/resetPassword`, userData).catch((error) => {
      seterrMessage(error.response.data.message)
      setLoading(false)
    })
    setLoading(false)
    if (data.token) {
      navigate("/login")
    }
  }



  return <>
    <div className="container py-5">
      <form onSubmit={formik.handleSubmit} className="w-75 m-auto">
        <h2>resetPassword :</h2>
        <div className="my-3">
          <label htmlFor="email">email :</label>
          <input onChange={formik.handleChange} type="email" id='email' className="form-control" />
          <p className="text-danger">{formik.errors.email}</p>
        </div>
        <div className="my-3">
          <label htmlFor="newPassword">newPassword :</label>
          <input onChange={formik.handleChange} type="password" id='newPassword' className="form-control" />
          <p className="text-danger">{formik.errors.newPassword}</p>
        </div>
        {errMessage != "" ? <div className='alert alert-danger'>{errMessage}</div> : ""}
        {Loading ? <button type="button" className="btn float-end bg-btn text-white"><i className='fa-solid fa-spinner fa-spin text-white'></i></button> : <button type="submit" disabled={!formik.isValid} className="btn float-end bg-btn text-white">Update Password</button>}
      </form>
    </div>
  </>
}
